

import { ServerDataSource } from 'ng2-smart-table';
import { RequestOptionsArgs } from '@angular/http/src/interfaces';
import { Http } from '@angular/http'
import { Injectable } from '@angular/core';
import { ToasterService } from 'angular2-toaster';
import 'style-loader!angular2-toaster/toaster.css';
export class RemoteDataSource extends ServerDataSource {
	private firstLoad: number = 0;
	// conf: any = {
	// 	sortFieldKey: '_sort',
	// 	sortDirKey: '_order',
	// 	pagerPageKey: '_page',
	// 	pagerLimitKey: '_limit',
	// 	filterFieldKey: '#field#',
	// 	totalKey: 'x-total-count',
	// 	dataKey: 'data',
	//   };

	getElements(): Promise<any> {
		if (this.firstLoad == 0) {
			this.firstLoad++;
			return super.getElements();
		} else if (
			(this.filterConf.filters) &&
			(this.filterConf.filters.length >= this.firstLoad)
		) {
			this.firstLoad++;
			return Promise.resolve(this.data);
		} else {
			var s =  super.getElements();
			console.log(s)
			return s
		}
	}
}

// @Injectable()
export class ActionConfirm {
	public url: string
	public toasterService : ToasterService
	constructor(private http: Http,toasterService: ToasterService ) {
        this.toasterService = toasterService;
		}
	public onCreateConfirm(event) {
		var toas = this.toasterService
		var model = event.newData
		this.http
			.post(this.url, model)
			.toPromise()
			.then(
			function (res) {
				toas.pop("success", "")
				event.confirm.resolve();
			}
			)
			.catch(function (err) {
				console.log(err["_body"])
				toas.pop("error", "", err["_body"])
				event.confirm.reject();
			})
	}
	public onDeleteConfirm(event): void {
		var toas = this.toasterService
         if (window.confirm('')) {
			console.log(event)
			this.http.delete(this.url+"/"+ event.data["id"]).toPromise()
			.then(function(res){
				toas.pop("success", "")
				event.confirm.resolve();	
			})
			.catch(
				function(err){
				    console.log(err)
					toas.pop("error", "", err["_body"])
					event.confirm.reject();	
				}
			)
        } else {
            event.confirm.reject();
        }
	}
	
	public onEditConfirm(event) {
		console.log(event)
		var toas = this.toasterService
		var model = event.newData
		this.http
			.put(this.url, model)
			.toPromise()
			.then(
			function (res) {
				toas.pop("success", "")
				event.confirm.resolve();
			}
			)
			.catch(function (err) {
				console.log(err)
				toas.pop("error", "", err["_body"])
				event.confirm.reject();
			})
	}
}


