import { Component } from '@angular/core';

import { NbMenuItem } from '@nebular/theme';
import {Permissions, AuthService} from "../auth/auth.service"
import {Http} from "@angular/http"
@Component({
  selector: 'ngx-pages',
  template: `
    <ngx-sample-layout>
      <nb-menu [items]="menu"></nb-menu>
      <router-outlet></router-outlet>
    </ngx-sample-layout>
  `,
  providers:[AuthService]
})
export class PagesComponent {
  private MENU_ITEMS: NbMenuItem[]
  private menu: NbMenuItem[]
  private role:string 
  constructor(private auth: AuthService){
      this.menu = []
      this.auth.getUser().then((res)=>{
        var data = res.json()
        this.role = data['type']
        console.log(this.role)
        this.initMenu()
        this.menu = this.MENU_ITEMS;
      }).catch((res)=>{
        console.log(res)
      })



  }
  
  
      
  isShow(targetRole: string):boolean {
    // console.log(this.role, targetRole, this.role != targetRole)
    return this.role != targetRole;
  }
  initMenu(){
    this.MENU_ITEMS = [
      {
        title: '',
        group: true,
        hidden: this.isShow("")
      },
      {title: '',icon:"fa fa-users", link: '/pages/admin/users', hidden: this.isShow("")},
      {title: '',icon:"fa fa-wrench", link: '/pages/admin/equipment',hidden: this.isShow("") },
      {title: '',icon:"fa fa-book", link: '/pages/admin/projects',  hidden: this.isShow("")   },
      {
        title: '', icon:"nb-cloudy",
        hidden: this.isShow(""),
        children: [
          // {
          //   title:"",
          //   link: '/pages/admin/gateway'
          // },
          {
            title:"",
            link: '/pages/admin/smokes',
            hidden: this.isShow("")
          },
        ]
    
      },
     
      {title: '',icon:"nb-home", link: '/pages/common-user/index',hidden: this.isShow("")  },
      {title: '',icon:"nb-grid-b-outline", link: '/pages/common-user/pic',hidden: this.isShow("") },
      {title: '',icon:"nb-location", link: '/pages/common-user/map',hidden: this.isShow("") },
      {
        title: '', icon:"nb-cloudy",
        children: [
          // {
          //   title:"",
          //   link: '/pages/common-user/equipment/gateway'
          // },
          {
            title:"",
            link: '/pages/common-user/equipment/smoke'
          },
        ]
        ,hidden: this.isShow("")
    
      },
      {
        title: '',
        group: true
        ,hidden: this.isShow("")
      }, 
      {title: '',icon:"fa fa-tasks", link: '/pages/support/padding' ,hidden: this.isShow("")},
      {title: '',icon:"fa fa-paperclip", link: '/pages/support/done',hidden: this.isShow("")},
    ];
  }
}


export function ConventULR(url){
  return url
}


